.c-countrySelect .c-countryValue {
  display: flex;
  align-items: center;
}
.c-countrySelect__select__option--is-selected {
  background: orange !important;
}
.c-countrySelect__select__option--is-focused {
  background: lightblue !important;
}
.c-countrySelect .c-countryOption {
  display: flex;
  align-items: center;
}
.c-countrySelect .c-countryOption__label {
  color: black;
  font-style: normal;
}
.c-countrySelect .c-countryOption img {
  width: 50px;
  height: 30px;
}
.c-countrySelect__select__control {
  border: 1px solid #78706A;
}
.c-countrySelect__select__indicator-separator {
  background-color: #78706A;
}
.c-countrySelect.skjemaelement__feilmelding .c-countrySelect__select__control {
  border: 1px solid #BA3A26;
}
.highContrast .c-countrySelect__select__control {
  border-width: 2px;
  border-color: #ffffff;
  background: black !important;
}
.highContrast .c-countrySelect__select__option--is-selected {
  background: #4990e2;
  color: #000000 !important;
  font-weight: bold;
}
.highContrast .c-countrySelect__select__option--is-focused {
  background: #ffc40d;
  color: #000000 !important;
  font-weight: bold;
}
.highContrast .c-countrySelect .c-countryOption {
  background: #000000;
}
.highContrast .c-countrySelect .skjemaelement__feilmelding .c-countrySelect__select__control {
  border-width: 2px;
  border-color: #BA3A26;
}
